import React, { FC } from 'react';
import { styled } from '@mui/material/styles';
import { Box, Divider, Typography } from '@mui/material';
import { SprintQuestionItem } from './SprintModel';
import ResultLine from './ResultLine';

const StyledBox = styled(Box)`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40%;
`;

const StyledTypografy = styled(Typography)`
  font-size: 24px;
  color: #202026;
`;

interface ResultsProps {
  correctAnswers: SprintQuestionItem[];
  incorrectAnswers: SprintQuestionItem[];
}
const Results: FC<ResultsProps> = ({ correctAnswers, incorrectAnswers }) => {
  const correct = correctAnswers.map(item => (
    <ResultLine
      audio={item.audio}
      translate={item.translate}
      word={item.word}
      key={item.wordId}
    />
  ));
  const incorrect = incorrectAnswers.map(item => (
    <ResultLine
      audio={item.audio}
      translate={item.translate}
      word={item.word}
      key={item.wordId}
    />
  ));
  return (
    <StyledBox>
      <StyledTypografy>Correct</StyledTypografy>
      {correct}
      <Divider />
      <StyledTypografy>Incorrect</StyledTypografy>
      {incorrect}
    </StyledBox>
  );
};

export default Results;
